  html, body, div, span, applet, object, iframe, h1, h2, h3, h4, h5, h6, p, blockquote, pre, a, abbr, acronym, address, big, cite, code, del, dfn, em, img, ins, kbd, q, s, samp, small, strike, strong, sub, sup, tt, var, b, u, i, center, dl, dt, dd, ol, ul, li, fieldset, form, label, legend, table, caption, tbody, tfoot, thead, tr, th, td, article, aside, canvas, details, embed, figure, figcaption, header, hgroup, menu, nav, output, ruby, section, summary, time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    vertical-align: baseline;
  }
  
  article, aside, details, figcaption, figure, header, hgroup, menu, nav, section {
    display: block;
  }
  
  ol, ul {
    list-style: none;
  }
  
  table {
    border-collapse: collapse;
    border-spacing: 0;
  }
  
  
  h1, h2, h3, h4, h5, h6 {
    margin: 10px 0;
    font-weight: 700;
    letter-spacing: -1px
  }
  
  h1 {
    font-size: 36px;
    font-weight: 700
  }
  
  h2 {
    padding-bottom: 10px;
    font-size: 32px;
    background: url("../img/home/bg_hr.png") repeat-x bottom
  }
  
  h3 {
    font-size: 24px
  }
  
  h4 {
    font-size: 21px
  }
  
  h5 {
    font-size: 18px
  }
  
  h6 {
    font-size: 16px
  }
  
  p {
    margin: 10px 0 15px 0
  }
  
  
  a {
    text-decoration: none;
    color: #0F79D0;
    text-shadow: none;
    transition: color 0.5s ease;
    transition: text-shadow 0.5s ease;
    -webkit-transition: color 0.5s ease;
    -webkit-transition: text-shadow 0.5s ease;
    -moz-transition: color 0.5s ease;
    -moz-transition: text-shadow 0.5s ease;
    -o-transition: color 0.5s ease;
    -o-transition: text-shadow 0.5s ease;
    -ms-transition: color 0.5s ease;
    -ms-transition: text-shadow 0.5s ease
  }
  
  a:hover, a:focus {
    text-decoration: underline
  }
  
  
  
  em, cite {
    font-style: italic
  }
  
  strong {
    font-weight: bold
  }
  
  img {
    position: relative;
    margin: 0 auto;
    max-width: 739px;
    padding: 5px;
    margin: 10px 0 10px 0;
  }
  
  p img {
    display: inline;
    margin: 0;
    padding: 0;
    vertical-align: middle;
    text-align: center;
    border: none
  }
  
  pre, code {
    color: #f5ff6c;
    background-color: #111;
    font-size: 14px;
    border-radius: 2px;
    -moz-border-radius: 2px;
    -webkit-border-radius: 2px
  }
  
  pre {
    overflow: auto;
    width: 100%;
  }
  
  code {
    padding: 3px;
    margin: 0 3px;
    width: 100%;
  }
  
  pre code {
    display: block;
    box-shadow: none
  }
  
  blockquote {
    color: #666;
    margin-bottom: 20px;
    padding: 0 0 0 20px;
    border-left: 3px solid #bbb
  }
  
  ul, ol, dl {
    margin-bottom: 15px
  }
  
  ul {
    list-style-position: inside;
    list-style: disc;
    padding-left: 20px
  }
  
  ol {
    list-style-position: inside;
    list-style: decimal;
    padding-left: 20px
  }
  
  dl dt {
    font-weight: bold
  }
  
  dl dd {
    padding-left: 20px;
    font-style: italic
  }
  
  dl p {
    padding-left: 20px;
    font-style: italic
  }
  
  hr {
