    padding: 10px;
    background: #373737;
    color: #fff
  }
  
  td {
    padding: 10px;
    border: 1px solid #373737
  }
  
  form {
    background: #f2f2f2;
    padding: 20px
  }
  
  .outer {
    width: 100%
  }
  
  .inner {
    position: relative;
    max-width: 640px;
    padding: 20px 10px;
    margin: 0 auto
  }
  
  #forkme_banner {
    display: block;
    position: absolute;
    top: 0;
    right: 10px;
    z-index: 10;
    padding: 10px 50px 10px 10px;
    color: #fff;
    background: url("../img/home/blacktocat.png") #0090ff no-repeat 95% 50%;
    font-weight: 700;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    border-bottom-left-radius: 2px;
    border-bottom-right-radius: 2px
  }
  
  #header_wrap {
    background: #212121;
    background: -moz-linear-gradient(top, #373737, #212121);
    background: -webkit-linear-gradient(top, #373737, #212121);
    background: -ms-linear-gradient(top, #373737, #212121);
    background: -o-linear-gradient(top, #373737, #212121);
    background: linear-gradient(to top, #373737, #212121)
  }
  
  #header_wrap .inner {
    padding: 50px 10px 30px 10px
  }
  
  #project_title {
    margin: 0 auto;
    color: #fff;
    font-size: 42px;
    font-weight: 700;
    text-shadow: #111 0px 0px 10px;
    max-width: 375px;
  }
  
  #project_tagline {
    color: #fff;
    font-size: 24px;
    font-weight: 300;
    background: none;
    max-width: 260px;
    text-shadow: #111 0px 0px 10px;
  }
  
  #downloads {
    position: absolute;
    width: 210px;
    z-index: 10;
    bottom: -40px;
    right: 0;
    height: 70px;
    background: url("../img/home/icon_download.png") no-repeat 0% 90%
  }
  
  .zip_download_link {
    display: block;
    float: right;
    width: 90px;
    height: 70px;
    text-indent: -5000px;
    overflow: hidden;
    background: url(../img/home/sprite_download.png) no-repeat bottom left
  }
  
  .tar_download_link {
    display: block;
    float: right;
    width: 90px;
    height: 70px;
    text-indent: -5000px;
    overflow: hidden;
    background: url(../img/home/sprite_download.png) no-repeat bottom right;
    margin-left: 10px
  }
  
  .zip_download_link:hover {
    background: url(../img/home/sprite_download.png) no-repeat top left
  }
  
  .tar_download_link:hover {
    background: url(../img/home/sprite_download.png) no-repeat top right
  }
  
  #main_content_wrap {
    background: #f2f2f2;
    border-top: 1px solid #111;
    border-bottom: 1px solid #111
  }
  
  #main_content {
    padding-top: 40px
  }
  
  #footer_wrap {
    position: fixed;
    bottom: 0;
    height: 120px;
    background: #212121
  }
  
  @media screen and (max-width: 992px) {
    img {
      max-width: 100%
    }
  }
  
  @media screen and (max-width: 480px) {
    body {
      font-size: 14px
    }
  
    #downloads {
      display: none
    }
  
    .inner {
      min-width: 320px;
      max-width: 480px
    }
  
    #project_title {
      font-size: 32px
    }
  
    h1 {
      font-size: 28px
