    h2 {
      font-size: 24px
    }
  
    h3 {
      font-size: 21px
    }
  
    h4 {
      font-size: 18px
    }
  
    h5 {
      font-size: 14px
    }
  
    h6 {
      font-size: 12px
    }
  
    code, pre {
      font-size: 11px
    }
  }
  
  @media screen and (max-width: 320px) {
    body {
      font-size: 14px
    }
  
    #downloads {
