    }
  
    h3 {
      font-size: 18px
    }
  
    h4 {
      font-size: 16px
    }
  
    h5 {
      font-size: 14px
    }
  
    h6 {
      font-size: 12px
    }
  
    code, pre {
      min-width: 240px;
      max-width: 320px;
      font-size: 11px;
    }
  }
  
